@charset "UTF-8";
#homeHeader {
  height: 80px;
}
#homeContents {
  display: flex;
  flex-flow: row;
  /*伸缩项目单行排列*/
  background: #e4e8f4;
}
#aside {
  flex: 0 0 160px;
}
#content {
  padding: 20px 32px;
  height: calc(100vh - 120px);
  flex: 1 1 1px;
}
@media screen and (max-height: 800px) {
#homeHeader {
    height: 70px;
}
#content {
    padding: 10px;
    height: calc(100vh - 90px);
    flex: 1 1 1px;
}
}
