@charset "UTF-8";
#home_aside {
  background: #dee2ed;
  width: 100%;
  height: 100%;
  position: relative;
}
#home_aside .aside-bar {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: #343C45;
    transition: all 0.3s;
    z-index: 4;
}
#home_aside .aside-bar .aside-nav {
      font-size: 18px;
      font-family: MicrosoftYaHei;
      background: #fff;
}
#home_aside .aside-bar .aside-nav .aside-item {
        height: 100px;
        line-height: 100px;
        position: relative;
        padding: 0;
}
#home_aside .aside-bar .aside-nav .aside-item a {
          display: block;
}
#home_aside .aside-bar .aside-nav .aside-item a .nav-name {
            color: #000;
            vertical-align: middle;
}
#home_aside .aside-bar .aside-nav .aside-item a .nav-icon {
            vertical-align: middle;
            padding-right: 10px;
            font-size: 20px;
            color: #436EF6;
            padding-left: 12px;
}
#home_aside .aside-bar .aside-nav .aside-item a .nav-icon2 {
            position: absolute;
            right: 10px;
            top: 0;
            opacity: 0;
            font-size: 20px;
            color: #436EF6;
}
#home_aside .aside-bar .aside-nav .aside-item a.active, #home_aside .aside-bar .aside-nav .aside-item a:hover {
            background: #798CA1;
}
#home_aside .aside-bar .aside-nav .aside-item a.active .nav-name, #home_aside .aside-bar .aside-nav .aside-item a.active .nav-icon, #home_aside .aside-bar .aside-nav .aside-item a.active .nav-icon2, #home_aside .aside-bar .aside-nav .aside-item a:hover .nav-name, #home_aside .aside-bar .aside-nav .aside-item a:hover .nav-icon, #home_aside .aside-bar .aside-nav .aside-item a:hover .nav-icon2 {
              color: #fff;
}
#home_aside .aside-bar .aside-nav .aside-item.active, #home_aside .aside-bar .aside-nav .aside-item:hover {
          background: #798CA1;
}
#home_aside .aside-bar .aside-nav .aside-item.active .nav-name, #home_aside .aside-bar .aside-nav .aside-item.active .nav-icon, #home_aside .aside-bar .aside-nav .aside-item.active .nav-icon2, #home_aside .aside-bar .aside-nav .aside-item:hover .nav-name, #home_aside .aside-bar .aside-nav .aside-item:hover .nav-icon, #home_aside .aside-bar .aside-nav .aside-item:hover .nav-icon2 {
            color: #fff;
}
#home_aside .aside-bar-move {
    left: -118px;
    transition: all 0.3s;
}
#home_aside .aside-bar-move .aside-nav .aside-item a .nav-name {
      opacity: 0;
}
#home_aside .aside-bar-move .aside-nav .aside-item a .nav-icon {
      opacity: 0;
}
#home_aside .aside-bar-move .aside-nav .aside-item a .nav-icon2 {
      opacity: 1;
}
.aside-bar .aside-nav .active a .nav-icon2 {
  color: #fff;
}
.child-list {
  height: 100%;
  width: 116px;
  font-size: 18px;
  position: absolute;
  left: 0;
  color: #000;
  background: #fff;
  z-index: -1;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.child-list-move {
  z-index: 3;
  left: 44px;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.toggle-btn-list {
  width: 16px;
  height: 40px;
  position: absolute;
  top: 50%;
  margin-top: -20px;
  right: 12px;
  cursor: pointer;
}
.child-name {
  height: 60px;
  line-height: 60px;
  text-align: center;
}
.child-name:hover, .child-name.active {
    background: #798CA1;
}
.child-name:hover a, .child-name.active a {
      color: #fff;
}
.child-name a {
  display: block;
  font-size: 17px;
  color: #000;
}
.child-name a.active {
    background: #798CA1;
    color: #fff;
}
@media screen and (max-height: 800px) {
.child-name {
    height: 50px;
    line-height: 50px;
}
}
@media screen and (max-height: 800px) {
  /*当屏幕尺寸高度小于800px时，应用下面的CSS样式*/
#home_aside .aside-bar .aside-nav {
    font-size: 17px;
}
#home_aside .aside-bar .aside-nav .aside-item {
      height: 80px;
      line-height: 80px;
}
#home_aside .aside-bar .aside-nav .aside-item a .nav-icon2 {
    top: 5px;
}
.child-name {
    height: 60px;
    line-height: 60px;
}
.child-list-0 li:first-child {
    margin-top: 0px;
}
.child-list-1 li:first-child {
    margin-top: 100px;
}
.child-list-2 li:first-child {
    margin-top: 200px;
}
.child-list-3 li:first-child {
    margin-top: 300px;
}
.child-list-4 li:first-child {
    margin-top: 400px;
}
.child-list-5 li:first-child {
    margin-top: 500px;
}
.child-list-6 li:first-child {
    margin-top: 600px;
}
}
@media (min-height: 801px) and (max-height: 900px) {
  /*当屏幕尺寸高度小于900px时，应用下面的CSS样式*/
#home_aside .aside-bar .aside-nav {
    font-size: 17px;
}
#home_aside .aside-bar .aside-nav .aside-item {
      height: 120px;
      line-height: 120px;
      padding-left: 12px;
}
#home_aside .aside-bar .aside-nav .aside-item a .nav-icon2 {
    top: 5px;
}
}
